	module.exports = {"pathContext":{"posts":[{"html":"<p>Once upon a midnight dreary, while I pondered, weak and weary,\nOver many a quaint and curious volume of forgotten lore—\nWhile I nodded, nearly napping, suddenly there came a tapping\nAs of some one gently rapping, rapping at my chamber door—\n\"'Tis some visitor,\" I muttered, \"tapping at my chamber door—\nOnly this and nothing more.\"</p>","id":"/Users/fernandomendoza/Proyectos/my-blog/src/pages/2017-12-11-the-raven/index.md absPath of file >>> MarkdownRemark","frontmatter":{"date":"2017-12-11T23:59:29+00:00","path":"/the-raven","title":"The Raven","excerpt":"Only this and nothing more","tags":["poem","narrative","edgar allan poe","poe"]}}],"tagName":"edgar allan poe"}}

